import React from "react";
import styled from "styled-components";
import UpdateWaveBackground from "../components/backgrounds/UpdateWaveBackground";
import PurchaseButton from "../components/buttons/PurchaseButton";
import Layout from "../components/layout/layout";
import SEO from "../components/layout/seo";
import { themes } from "../components/styles/ColorStyles";
import {
    BodyMain,
    Caption,
    Caption2,
    H1,
    H3,
    MediumText,
    SmallText2,
} from "../components/styles/TextStyles";

function update() {
    return (
        <Layout>
            <SEO title="Updates" />
            <Wrapper>
                <UpdateWaveBackground />
                <ContentWrapper>
                    <ContentTop>
                        <H1>Updates</H1>
                        <BodyMain>
                            Excepteur sint occaecat cupidatat non proident, sunt
                            in culpa
                        </BodyMain>
                    </ContentTop>
                </ContentWrapper>
                <Intro>
                    <div></div>
                    <IntroText>
                        <div>
                            <H1>Gravida rutrum quisque</H1>
                            <MediumText>
                                Ultrices eros in cursus turpis massa. Eu non
                                diam phasellus vestibulum lorem fames ac turpis
                                egestas maecenas pharetra convallis.
                            </MediumText>
                            <PurchaseButton title="More details" />
                        </div>
                        <div>
                            <H3>Proin fermentum</H3>
                            <MediumText>
                                New devices and cards for Sketch and Figma
                                libraries and XD files
                            </MediumText>
                            <MediumText>
                                New devices and cards for Sketch and Figma
                                libraries and XD files
                            </MediumText>
                            <MediumText>
                                Lacus luctus accumsan tortor posuere ac ut
                                consequat semper viverra.
                            </MediumText>
                            <MediumText>
                                Bibendum est ultricies integer quis auctor elit
                                sed vulputate mi.
                            </MediumText>
                            <MediumText>
                                New devices and cards for Sketch and Figma
                                libraries and XD files
                            </MediumText>
                            <MediumText>
                                Habitasse platea dictumst quisque sagittis purus
                                sit amet.
                            </MediumText>
                        </div>
                    </IntroText>
                </Intro>
                <CardsContainer>
                    <Card>
                        <img src="/images/covers/cover.png" alt="cover" />
                        <CardText>
                            <div>
                                <Caption2>24.11.2021</Caption2>
                                <H3>Caecat - cupidatat non proident sunt.</H3>
                                <MediumText>
                                    We have added the new MacBook 14 inch and
                                    16inch, Apple Watch Series 7, Pixel 6 and
                                    new cards comps!
                                </MediumText>
                            </div>
                            <div>
                                <H3>Improvements</H3>
                                <MediumText>
                                    New devices and cards for Sketch and Figma
                                    libraries and XD files
                                </MediumText>
                                <MediumText>
                                    New devices and cards for Sketch and Figma
                                    libraries and XD files
                                </MediumText>
                            </div>
                        </CardText>
                    </Card>
                    <Card>
                        <img src="/images/covers/cover_1.png" alt="cover" />
                        <CardText>
                            <div>
                                <Caption2>24.11.2021</Caption2>
                                <H3>Deserunt mollit anim.</H3>
                                <MediumText>
                                    Ut enim ad minim veniam, quis nostrud
                                    exercitation ullamco laboris nisi ut aliquip
                                    ex ea commodo consequat.
                                </MediumText>
                            </div>
                            <div>
                                <H3>Improvements</H3>
                                <MediumText>
                                    Elementum tempus egestas sed sed risus
                                    pretium quam vulputate dignissim. Ipsum
                                    dolor sit amet consectetur adipiscing elit
                                    ut. At tempor commodo ullamcorper a lacus
                                    vestibulum sed arcu. Velit laoreet id donec
                                    ultrices tincidunt arcu non. Eu scelerisque
                                    felis imperdiet proin fermentum leo.
                                </MediumText>
                            </div>
                        </CardText>
                    </Card>
                    <PurchaseButton title="Show more" />
                </CardsContainer>
            </Wrapper>
        </Layout>
    );
}

const Wrapper = styled.div`
    overflow: hidden;
    color: #fff;
`;

const ContentWrapper = styled.div`
    max-width: 1234px;
    margin: 0 auto;
    padding: 200px 30px 100px;
    color: ${themes.dark.text1};
    @media (max-width: 767px) {
        padding: 100px 30px;
    }
`;

const Intro = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 1234px;
    margin: 0 auto;
    padding: 0px 30px 120px;
    @media (max-width: 768px) {
        grid-template-columns: 1fr;
        padding: 0px 30px;
    }
`;
const IntroText = styled.div`
    grid-template-columns: 1fr 1fr;
    display: grid;
    gap: 20px;
    background: rgba(195, 195, 247, 0.4);
    box-shadow: 0px 50px 100px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(10px);
    padding: 20px;
    border-radius: 20px;
    @media (max-width: 768px) {
        grid-template-columns: 1fr;
    }
    > div {
        display: grid;
        gap: 20px;
        height: fit-content;
    }
`;

const ContentTop = styled.div`
    max-width: 400px;
    margin: auto;
    text-align: center;
    display: grid;
    gap: 10px;
`;

const CardsContainer = styled.div`
    display: grid;
    gap: 80px;
    max-width: 1234px;
    margin: 0 auto;
    justify-items: center;
    padding: 100px 30px;
`;

const Card = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr;
    img {
        max-width: 450px;
    }

    @media (max-width: 820px) {
        grid-template-columns: auto;
        justify-items: center;
        gap: 20px;
    }

    @media (max-width: 768px) {
        grid-template-columns: auto;
        img {
            max-width: 100%;
        }
    }
`;

const CardText = styled.div`
    padding: 20px;
    border-radius: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    @media (max-width: 768px) {
        grid-template-columns: auto;
    }
    > div {
        display: grid;
        gap: 15px;
        height: fit-content;
    }
`;
export default update;
